.cta__call-modal { position: relative; flex: 0 0 235px; display: flex; justify-content: center; align-items: center; text-align: center; margin-left: auto; size: 235px; color: white; font-size: 30px; font-weight: 900; cursor: pointer; transform: scale(0); transition: transform .4s 1s ease, opacity .3s 1s ease; }

.cta__call-modal span { display: inline-block; position: relative; z-index: 2; transition: transform 0.25s ease-in-out, opacity 0.15s 0.1s ease; }

.cta__call-modal .ico-close { position: absolute; top: 50%; left: 50%; z-index: 10; transform: translate(-50%, -50%) scale(0); opacity: 0; transition: transform 0.25s ease-in-out, opacity 0.15s 0.1s ease; cursor: pointer; pointer-events: none; }

.cta__call-modal::before, .cta__call-modal::after { display: inline-block; content: ' '; position: absolute; size: 235px; top: 50%; left: 50%; transform: translate(-50%, -50%); background: #95ca6e; border-radius: 50%; }

.cta__call-modal::before { opacity: 0; pointer-events: none; transition: transform .5s .1s ease-in; z-index: 1; }

.cta__call-modal::after { z-index: 1; background: #95ca6e; border-radius: 50%; animation-name: ctaPulse; animation-duration: 2000ms; animation-timing-function: ease; animation-iteration-count: infinite; }

.cta__call-modal:hover::after { animation-play-state: paused; }

.cta__call-modal.active span { opacity: 0; transform: scale(0); }

.cta__call-modal.active .ico-close { transform: translate(-50%, -50%) scale(1); opacity: 1; pointer-events: all; }

.cta__call-modal.active .ico-close:hover { transform: translate(-50%, -50%) scale(1.1) rotate(-45deg); }

.cta__call-modal.active::before { opacity: 1; transform: translate(-50%, -50%) scale(15); }

.cta__blot { position: absolute; background: #f2f2f2; border-radius: 50%; transform: scale(0); }

.cta__blot--top { size: 135px; top: -20px; left: 50%; margin-left: -67px; }

.cta__blot--middle { size: 32px; left: 63%; top: 37%; }

.cta__blot--bottom { size: 320px; left: 25%; bottom: 0; transform: scale(0) translateY(250px); }

@media only screen and (max-width: 992px) { .cta__text { flex: 0 1 380px; font-size: 25px; }
  .cta__right { flex: 0 0 auto; margin-left: auto; }
  .cta__figure__number { font-size: 75px; }
  .cta__call-modal { flex: 0 0 210px; size: 210px; font-size: 28px; }
  .cta__call-modal::before, .cta__call-modal::after { size: 210px; } }

@media only screen and (max-width: 870px) { .cta__text { flex: 0 1 350px; font-size: 22px; }
  .cta__figure { margin-bottom: 25px; }
  .cta__figure__number { font-size: 68px; }
  .cta__call-modal { flex: 0 0 190px; size: 190px; font-size: 26px; }
  .cta__call-modal::before, .cta__call-modal::after { size: 190px; } }

@media only screen and (max-width: 768px) { .cta__wrapper { flex-direction: column; max-width: 450px; margin-left: auto; margin-right: auto; }
  .cta__text { flex: 0 0 auto; padding-right: 0; }
  .cta__right { margin-top: 20px; flex: 0 0 auto; padding-left: 0; margin-left: 0; align-items: center; } }

@media only screen and (max-width: 414px) { .cta__text { font-size: 20px; }
  .cta__right { flex-direction: column; align-items: flex-start; }
  .cta__call-modal { margin-left: auto; margin-right: auto; } }

.section.active .hero__image::after { transform: translateX(100%); opacity: 0; }

.section.active .hero__text { opacity: 1; }

.section.active .hero .next-section span, .section.active .hero .next-section .ico { transform: translate3d(0, 0px, 0); }

.section.active .hero .next-section:hover .ico { transform: scale(1.2); }

.section.active .info__description { transform: translate3d(0, 0, 0); opacity: 1; }

.section.active .info__blot { transform: scale(1); }

.section.active .info .next-section span, .section.active .info .next-section .ico { opacity: 1; transform: translate3d(0, 0px, 0); }

.section.active .info .next-section:hover .ico { transform: scale(1.2); }

.section.active .slick-next, .section.active .slick-prev { animation-name: ctaBlot; animation-duration: .6s; animation-timing-function: ease; animation-fill-mode: forwards; }

.section.active .slick-prev { animation-delay: .6s; }

.section.active .slick-next { animation-delay: .3s; }

.section.active .cta__blot { animation-name: ctaBlot; animation-duration: .8s; animation-timing-function: ease; animation-fill-mode: forwards; }

.section.active .cta__blot--middle { animation-delay: .5s; }

.section.active .cta__blot--bottom { animation-name: ctaBlotBottom; animation-delay: 1.3s; }

.section.active .cta__text { opacity: 1; }

.section.active .cta__figure__number { opacity: 1; transform: translate3d(0, 0, 0); }

.section.active .cta__figure__description { opacity: 1; }

.section.active .cta__call-modal { animation-name: ctaBlot; animation-duration: .8s; animation-delay: 2s; animation-timing-function: ease; animation-fill-mode: forwards; }

.preloader { display: flex; position: fixed 0; z-index: 9; pointer-events: none; }

.preloader__wrapper { position: relative; height: 100vh; width: 100%; }

.preloader__blot { display: block; content: ' '; position: absolute; top: calc(50% - 40px); left: 50%; size: 30px; background: #f2f2f2; border-radius: 50%; z-index: 5; transform: translate(-50%, -50%) scale(1); animation: preloaderBlot .5s ease; animation-fill-mode: forwards; }

.preloader__image { position: absolute; top: 50%; left: 50%; width: 100%; opacity: 0; transform: translate3d(-50%, -50%, 0); z-index: 6; -webkit-clip-path: circle(0px at center); clip-path: circle(0px at center); }

.preloader__image img { min-width: 100%; min-height: 100%; width: auto; height: auto; }

.preloader__circle { position: absolute; top: 50%; left: 50%; transform: translate3d(-50%, -50%, 0) scale(1); z-index: 7; opacity: 0; border-radius: 50%; size: 60px; background: white; transition: transform .3s linear; }

.preloader.stage2 .preloader__image { opacity: 1; animation: preloaderImage .6s linear; }

.preloader.stage2 .preloader__circle { transform: translate3d(-50%, -50%, 0) scale(0.5); opacity: 1; animation: preloaderCircle .5s .3s linear; }
