// HEADER
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  opacity: 0;
  transition: all $transitionSpeed $ease;
  z-index: 9;
  pointer-events: none;
  &.visible {
    opacity: 1; }
  &__wrapper {
    padding: 50px 0;
    display: flex;
    align-items: center; }
  &__logo {
    flex: 0 0 auto;
    max-width: 100px;
    img {
      max-width: 100%; } }
  &__text {
    width: 100%;
    position: relative;
    font-size: 40px;
    font-weight: 700;
    color: white;
    margin-left: 25px;
    line-height: .7;
    opacity: 0;
    height: 33px;
    transform: translate3d(0,-100%,0);
    transition: transform $transitionSpeed $ease, opacity .15s .1s ease;
    &.showing {
      opacity: 1;
      transform: translate3d(0,0,0); }
    span {
      position: absolute;
      left: 0;
      top: 0;
      transform: translate3d(30px,0,0);
      opacity: 0;
      transition: all .3s ease;
      &.showing {
        opacity: 1;
        transform: translate3d(0,0,0); }
      &.removing {
        opacity: 0;
        transform: translate3d(-30px,0,0); } } } }

@include r($tablet) {
  .header {
    &__text {
      font-size: 34px;
      line-height: 1; } } }

@include r($mobile) {
  .header {
    &__wrapper {
      padding: 35px 0;
      flex-wrap: wrap; }
    &__text {
      margin-top: 10px;
      margin-left: 0px; } } }
